@import url('https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i');
$font-stack: Roboto, sans-serif;

$black-text       : #000;
$white-text       : #FAFAFA;
$grey-text        : #8C8C8C;
$grey-light-text  : #B5B5B5;
$disabled-text    : #C3C3C3;
$placeholder-text : #CACACA;

$primary-color    : #448AFF;
$lighter-color    : #82B1FF;
$darker-color     : #2962FF;
$background-color : #FAFAFA;
$white-color      : #FFF;

$bold     : bold;
$default  : default;

$right : right;
$left  : left;

$inline-block : inline-block;

@mixin padding-style($top, $left, $bottom, $right) {
  padding: $top $left $bottom $right; }

@mixin button-style($bg-color) {
  @include padding-style(8px, 16px, 8px, 16px);
  border-radius: 2px;
  background-color: $bg-color; }

@mixin text-style($size, $weigth, $style, $align, $spacing, $color) {
  font-size: $size;
  font-weight: $weigth;
  font-style: $style;
  text-align: $align;
  letter-spacing: $spacing;
  color: $color; }

@mixin flex-align-items($align) {
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
  align-items: $align; }

.button {
  @include button-style(#efefef);
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden; }

.button:hover {
  @include button-style(#dedede); }

.button-link {
  @include text-style(14px, 400, normal, center, .5px, $primary-color); }

* {
  -webkit-box-sizing  : border-box;
  -moz-box-sizing     : border-box;
  box-sizing          : border-box;
  margin  : 0;
  padding : 0;
  border  : 0; }

html, body {
  font-family: $font-stack;
  background-color: $background-color; }

nav {
  display: flex;
  display: -webkit-flex;
  display: -ms-flexbox;
  align-items: center;
  height: 56px;
  background-color: $primary-color;
  padding: 0 16px;
  color: $white-text;
  box-shadow: 0 2px 5px rgba(0,0,0,.26);
  div {
    margin: 0 16px; }
  a {
    color: $white-text;
    text-decoration: none;
    font-weight: $bold;
    border-radius: 2px;
    span {
      img {
        border-radius: 50%; } } }
  ul {
    display: flex;
    display: -webkit-flex;
    display: -ms-flexbox;
    align-items: center;
    list-style: none;
    li {
      display: inline-block;
      margin: 0 12px;
      a {
        letter-spacing: 2px;
        height: 56px; } } } }

.border-right {
  border-right: 1px solid #DEDEDE; }

.float-right {
  float: right; }

.center {
  margin: 0 auto; }

.search {
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  text-align: center;
  form {
    position: relative;
    div {
      display: inline-flex;
      align-items: center;
      box-shadow: 0 1px 2px rgba(0,0,0,.26);
      label {
        display: none; }
      input {
        border-radius: 2px 0 0 2px;
        min-width: 400px;
        padding: 10px 0 10px 10px;
        background-color: $background-color;
        font-size: 16px;
        line-height: 20px; }
      button {
        background-color: $background-color;
        border-radius: 0 2px 2px 0;
        color: #9A9A9A;
        font-size: 16px;
        cursor: pointer;
        i {
          line-height: 20px;
          padding: 10px; } } } } }

ul {
  list-style: none;
  li {} }

.border-radius {
  border-radius: 2px 0 0 2px; }

.logo a {
  font-size: 20px;
  background-color: transparent;
  border-radius: 2px;
  padding: initial;
  box-shadow: initial; }

button:focus,
input:focus {
  outline: thin dotted;
  outline: 0px auto -webkit-focus-ring-color;
  outline-offset: 0px; }

::-webkit-input-placeholder {
   color: $placeholder-text; }
:-moz-placeholder {
   color: $placeholder-text; }
::-moz-placeholder {
   color: $placeholder-text; }
:-ms-input-placeholder {
   color: $placeholder-text; }

.card {
  @extend .box-shadow;
  @extend .border-radius;
  position: relative;
  background-color: $white-color; }

.card-img {
  cursor: pointer;
  position: relative;
  overflow: hidden;
  height: 200px;
  @extend .border-radius;
  img {}
  margin: auto;
  min-width: 50%;
  min-height: 50%; }

.card-container {
  padding: 8px 16px;
  .card-title {
    position: relative;
    @include flex-align-items(center);
    div {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      background: linear-gradient(to right, transparent , #FFF);
      width: 80px; }
    span {
      font-size: 18px;
      overflow: hidden;
      white-space: nowrap;
      width: 100%; }
    i {
      color: $grey-text; }
    a {
      z-index: 1;
      padding-left: 5px; } }

  .card-star {
    @include flex-align-items(flex-start);
    padding: 8px 0 16px 0;
    .star {
      i {
        color: #ffd652; } }
    a {
      text-decoration: none;
      font-size: 16px;
      i {
        font-size: 16px;
        color: #b5b5b5; } }
    span {
      padding-left: 8px;
      color: $grey-text;
      font-size: 14px; } }
  .location {
    @include flex-align-items(center);
    font-size: 12px;
    font-weight: 600;
    color: $grey-text;
    i {
      padding-right: 6px;
      font-size: 16px;
      color: $grey-text; } }
  .price {
    @include flex-align-items(center);
    padding-top: 8px;
    span {
      font-size: 16px;
      color: #3e3e3e;
      small {
        font-size: 12px;
        color: #8c8c8c; } } }
  p {
    margin: 8px 0;
    font-size: 13px;
    color: $grey-text;
    height: 46px;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box; } }

.card-action {
  padding: 0 16px;
  .action {
    @include flex-align-items(center);
    border-top: 1px solid #DEDEDE;
    padding: 8px 0;
    a {
      text-decoration: none; }
    span {
      @include flex-align-items(center);
      color: #8c8c8c;
      font-size: 14px;
      i {
        font-size: 16px;
        padding-right: 6px; } } } }

//@media screen and (max-width : 250px)

.divider {
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1; }

.box-shadow {
  box-shadow: 0 3px 5px rgba(0,0,0,.26); }

.border-radius {
  border-radius: 2px; }
